
.Section {
    display: flex; 
    flex-direction: column; 
    width: 80vw;
    padding-left: 10vw;
    padding-right: 10vw;
    padding-bottom: 80px;
    text-align: left;
    line-height: 2;
}

.AboutMeSection p {
    width: 65%;
    margin-right: 5%;
}

.profilePicture {
    height: 20vw;
    width: 20vw;
    border-radius: 10vw;
    overflow: hidden;
    object-fit: cover;
}

.sideBar {
    position: sticky;
    top: 110px;
    width: 20vw;
    height: 100%;
}

.sideBar a {
    color: #2980b9;
}

.Section h2 {
    line-height: 1;
    position: sticky;
    top: 0;
    background-color: white;
    padding-bottom: 20px;
    margin-left: -10vw;
    padding-left: 10vw;
    margin-right: -10vw;
    z-index: 10;
}

.Card {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    flex: 1;
    padding: 20px 20px 20px 20px;
    box-sizing: border-box;
    transition: all 0.5s;
}

.Card p {
    margin-right: 5%;
    transition: all 0.3s;
}

.Card:hover {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.SkillCards {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
}

.LearnMore {
    background-color: #2ecc71;
    color: white;
}

.LearnMore:hover {
    scale: 1.05;
    background-color: #27ae60;
}

.ContactSection {
    background-color: #34495e;
    color: white;
}

.ContactSection h2 {
    background-color: #34495e;
}


.CertificationsGrid {
    grid-template-columns: 1fr 1fr;
}

.CertificationCard {
    display: grid;
    grid-template-columns: 1fr 5fr 1fr;
}

.CertificationCard img {
    margin-top: auto;
    margin-bottom: auto;
    width: 80%;
    overflow: hidden;
    border-radius: 5%;
}

.CertificationCard span {
    margin-top: auto;
    font-size: 60px;
}

.CardGrid {
    display: grid;
    gap: 25px;
}

.EducationGrid {
    grid-template-columns: 1fr 1fr;
}

.LanguagesGrid {
    grid-template-columns: 1fr 1fr 1fr 1fr;
}