:root {
    --landing-section-positioning: fixed;
    --filler-height-1: 0vh;
    --filler-height-2: 200vh;
    --welcome-opacity: 1;
    --letter-rotation: 45deg;
}

.LandingSection {
    position: var(--landing-section-positioning);
    width: 100vw;
    height: 100vh;
    align-items: center;
    display: flex;
}

.LandingSectionFiller1 {
    position: relative;
    height: var(--filler-height-1);
}

.LandingSectionFiller2 {
    position: relative;
    height: var(--filler-height-2);
}

.NameContainer {
    width: calc(100vw * 13/15);
    margin-left: calc(100vw / 15);
    display: flex;
    flex-direction: row;
}

.Letter {
    z-index: 100000;
    width: calc(100% / 6);
    transition: all 0.5s;
}

.Letter:hover {
    scale: 1.5;
    rotate: var(--letter-rotation);
}

.WordContainer {
    display: flex;
    width: 100%;
}

.WordSpacer {
    width: calc(100vw * 2 / 15);
    height: 100px;
}

.BackgroundAnimation {
    width: 100vw;
    position: absolute;
}

.ScrollIndicator {
    width: 100px; 
    height: 100px;
    align-items: bottom; 
    margin-left: calc(50vw - 50px);
    justify-content: center;
    position: absolute;
    margin-top: calc(100vh - 140px);
}

.WelcomeText {
    line-height: 100vh; 
    text-align: center;
    width: 100vw;
    position: absolute;
    z-index: 1000;
    opacity: var(--welcome-opacity);
}

@media only screen and (max-width: 1000px) {
    .NameContainer {
        flex-direction: column;
    }
}











