.projectsContainer {
    display: inline-grid;
    grid-template-columns: 1fr  1fr;
    scroll-snap-align: start;
  }
  
.projectsDetail {
    height: 100vh;
    width: 50vw;
}
  
.projectsList {
    margin-top: 20%;
    padding-right: 5vw;
    height: auto;
    width: 45vw;
    max-width: 450px;
    text-align: left;
}
  
.largeSeparator {
    height: 1px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.1);
}
  
.projectIcon {
    height: 40px;
    width: 40px;
}
  
.projectName {
    float: left;
}
  
.projectRow {
    display: flex;
    justify-content: space-between;
    align-content: center;
    padding-top: 7px;
    padding-bottom: 7px;
    transition: scale 0.05s ease-out 100ms;
    height: 100%;
}

.projectRow:hover {
    scale: 1.02;
}
  
.projectRowPart {
    display: flex;  
}
  
.projectRowPart p {
    align-self: flex-end;
    margin-bottom: 15px;
    opacity: 0.3;
    font-size: small;
    margin-right: 5px;
}
  
.projectRowPart h3 {
    align-self: flex-end;
}
  
button {
    all: unset;
    cursor: pointer;
    width: 100%;
    transition: all 0.1s;
}

.ProjectDetailContainer {
    height:100%;
    width: 100%;
    padding-top: 10%;
    padding-bottom: 10%;
    box-sizing: border-box;
}

.ProjectDescriptionContainer {
    height: 35%;
    padding-left: 10%;
    padding-right: 10%;
    width: 80%;
}


.ProjectDescription {
    padding-top: 20px;
    text-align: justify;
    line-height: 1.7;
    
}

.slide {
    transform: scale(0.7);
    transition: transform 300ms;
    opacity: 0.5;
    z-index: -1000;
    /*border-radius: 50px;
    background-color: red;
    overflow: hidden;*/
}

.slideWrapper {
    height: 50vh;
    display: flex;
    justify-content: center;
}

.prevArrow {
    position: absolute;
    top: 40%;
    left: -50px;
    z-index: 100;
    cursor: pointer;
    font-size: 2rem;
}

.nextArrow {
    position: absolute;
    top: 40%;
    right: -50px;
    z-index: 100;
    cursor: pointer;
    font-size: 2rem;
}

.container {
    margin-left: 100px;
    margin-right: 100px;
}



@media only screen and (max-width: 1300px) {
    .slide {
        opacity: 0;
    }
}

@media only screen and (max-width: 800px) {

    .projectsContainer {
        display: flex;
        flex-flow: column;
        grid-template-rows: 1fr  1fr;
        padding-top: 0;
    }

    .projectsList {
        width: 80vw;
        margin-left: 9vw;
        margin-bottom: 50px;
        order: 1;
        max-width: 100vw;
    }

    .projectsDetail {
        width: 100vw;
        order: 2;
    }

    .ProjectDetailContainer {
        padding-top: 0;
    }

}