@import url('https://fonts.googleapis.com/css?family=K2D:thin,regular,bold,italic&subset=latin,latin-ext');


body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'K2D';
}

.App {
  text-align: center;
  /* hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none;
  scrollbar-width: none;
  background-color: white;
}

*{
  scroll-behavior: smooth;
  margin: 0;
}

/* hide scrollbar for chrome, safari and opera */
::-webkit-scrollbar {
  display: none;
}

/* Google Icon */
.material-symbols-outlined {
  font-variation-settings:
  'FILL' 1,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48
}

h1 {
  font-size: 50px;
}

h2 {
  font-size: 38px;
}

h3 {
  font-size: 23px;
}

h4 {
  font-size: 19px;
}

p {
  font-size: 19px;
}

a {
  text-decoration: none;
  transition: all 0.1s;
}

a:hover {
  opacity: 0.5;
}

.ContactSectionColumns {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.ContactSectionColumn {
  display: flex;
  flex-direction: column;
}

.ContactSectionColumn a {
  display: table-cell;
  color: white;
}


.burgerMenuButton {
  margin-top: 6px;
  margin-left: 6px;
  height: 28px;
  width: 28px;
  background-color: white;
  z-index: 1000;
}

.MenuContainer {
  background-color: white;
  border: none;
  padding: 10px;
  border-radius: 30px;
  margin-top: 20px;
  box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, .15);
  margin-left: 20px;
  position: fixed;
  z-index: 10000;
  display: flex;
  flex-direction: column;
  width: 40px;
  height: 40px;
  float: left;
  overflow: hidden;
  transition: all 0.2s ease-in-out;

}

.MenuContainer.expanded {
  width: 135px;
  height: 250px;
  border-radius: 10px;
}

.page_references {
  float: left;
  text-align: left;
  margin-left: 10px;
  color: black;
  text-decoration: none;
}

.LinkContainer {
  width: 100%;
  height:100%;
  justify-content: space-evenly;
  display: flex;
  flex-direction: column;
  float: left;
}

.languageContainer {
  display: flex;
  width: 100%;
  max-height: 15vh;
  align-items: center;
  text-align: left;
  margin-top: 25px;
}

.languageSectionGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  height: auto;
  width: 100%;
  margin-top: -15px;
}


@media only screen and (max-width: 1000px) {
  .ContactSectionColumns {
    grid-template-columns: 1fr 3fr 3fr 3fr 1fr;
  }
}

@media only screen and (max-width: 600px) {
  .ContactSectionColumns {
    grid-template-columns: 0fr 1fr 1fr 1fr 0fr;
  }

  .languageSectionGrid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
  }
}